* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

main {
  min-height: 100vh;
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  outline: none;
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  box-shadow: 0px 5px rgb(0,0,0,0.2);
  background-color: rgb(255, 255, 255, 0.5);
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgb(255,255,255,0.75);
}

.location-box .location {
  color: #000000;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgb(50 50 70 0.5);
}

.location-box .date {
  color: #000000;
  font-size: 20px;
  text-align: center;
  font-weight: 300;
  text-shadow: 2px 2px rgb(50 50 70 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  margin: 30px auto;
  display: inline-block;
  border-radius: 16px;
  background-color: rgb(255, 255, 255, 0.2);
  padding: 15px 25px;
  color: #ffffff;
  font-size: 102px;
  font-weight: 900;
  text-align: center;
  text-shadow: 3px 6px rgb(50 50 70 0.5);
  box-shadow: 3px 6px rgb(0,0,0,0.2);
}

.weather-box .weather {
  font-size: 48px;
  font-weight: 700;
  color: #ffffff;
  text-shadow: 3px 6px rgb(50 50 70 0.5);
}

.weather-box .details {
  font-size: 20px;
  font-weight: 400;
  color: #ffffff;
  text-shadow: 2px 2px rgb(50 50 70 0.5);
  margin-top: 20px;
}

